@import '../../styles/global.scss';

.button {
  color: white;
  display: inline-block;
  padding: .4em .8rem;
  font-size: 1.8rem;
  font-weight: 500;
  border: 3px solid transparent;
  border-radius: .2rem;
  text-transform: uppercase;
  transition: .2s all ease-in-out;

  &:hover, &:active {
    filter: brightness(95%);
  }
}